import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DefaultComponent } from '../../../default/default.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../layouts/layout.module';
import { AttributeValueComponent } from '../attribute-value/attribute-value.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: AttributeValueComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, FormsModule, ReactiveFormsModule, NgxPaginationModule,
    ],
    declarations: [AttributeValueComponent],
    // providers:[DataService]
})
export class AttributeValueModule { }
