import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators, ReactiveFormsModule } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-attribute-value',
    templateUrl: "attribute-value.component.html",
    styles: []
})
export class AttributeValueComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder, private _dataService: DataService, private _productService: ProductService) { }

    public attributeData = '';
    public user_id = '';
    public item_text: any;
    public item_id = 0;
    public attrData: any;
    public noOfPage = 0;
    public attrVal = '';
    public filterValue = '';
    public btn_text = 'Save';
    public form_title = 'Add New';
    public id = 0;
    public att_id = 0;
    public index_val = 0;
    public filterDataHolder: any;
    p: number = 1;

    currentUser: any;
    ngOnInit() {

        this.form = this.formBuilder.group({
            id: [null],
            index_val: [null],
            item_id: [null],
            item_text: [null, Validators.required],
        });


        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));

        this.user_id = this.currentUser.success.id;


        this.route.params.subscribe(
            params => {
                if (params.att_id) {
                    this.id = params.att_id;
                    this.att_id = params.att_id;
                    this.getAttrByID(this.id);
                }
            }
        );


    }

    onSubmitAddAttr() {
        this.formSumitAttempt = true;
        let input: any;
        input = { form_data: this.form.value };
        console.log(input.form_data.id);
        if (this.form.valid) {
            if (input.form_data.item_id == null || input.form_data.item_id == 0) {

                this._productService.addAttributeValue(input).subscribe(
                    data => {
                        //this.attributeData = data.success;
                        this.toastr.success('Attribute Value Added Successfully', 'Success Message');

                        // that.form.c;
                        this.getAttrByID(this.id);
                        this.reset();
                    }
                );


            } else {
                this._productService.updateAttributeValue(input).subscribe(
                    data => {
                        //this.attributeData = data.success;
                        this.toastr.success('Attribute Value Updated Successfully', 'Success Message');
                        this.getAttrByID(this.id);
                        this.reset();
                        this.btn_text = "Add";
                        this.form_title = "Add New";
                    }
                );
            }

        }


    }




    getAttrByID(att_id) {
        this._productService.getAttributeListsById(att_id).subscribe(
            data => {
                this.attrData = JSON.parse(data.success.data);
                this.noOfPage = this.attrData.length;
            }
        );
    }

    getAttrValByIndex(index_val) {
        this._productService.getAttrValByIndex(this.id, index_val).subscribe(
            data => {
                if (data.success.item_text) {
                    this.index_val = index_val;
                    this.item_id = data.success.item_id;
                    this.item_text = data.success.item_text;
                    this.btn_text = 'Update';
                    this.form_title = 'Update';
                }
            }
        );
    }

    deleteAttributeValue(index_val) {
        let value = confirm('Are you want to delete this attribute value ?');
        if (value) {
            this._productService.deleteAttributeValue(this.id, index_val).subscribe(
                data => {
                    this.toastr.success('Attribute Value Deleted Successfully', 'Success Message');
                    this._productService.getAttributes().subscribe(
                        data => {
                            this.attrData = data.success;
                            this.getAttrByID(this.id);
                        }
                    );
                }
            );
        }
    }


    reset() {
        // if (this.selectedStatus != '') {

        this.form.reset({ id: this.id });
        this.formSumitAttempt = false;

        // }
    }

    filterByValue() {

        let attr_value_data = [];
        if (this.filterValue == '') {
            this.attrData = this.filterDataHolder;
        } else {
            if (this.filterDataHolder == undefined) {
                this.filterDataHolder = this.attrData;
            } else {
                this.attrData = this.filterDataHolder;
            }

            this.attrData.forEach(obj => {
                //console.log(obj);
                if (obj.item_text != '' && obj.item_text != null && obj.item_text != undefined) {
                    if (obj.item_text.replace(/<[^>]*>/g, "").toUpperCase().indexOf(this.filterValue.toUpperCase()) > -1) {

                        attr_value_data.push(obj);
                    }
                }
            });
            this.attrData = attr_value_data;
        }
    }


}

